.notification-page-wrapper {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 60px); /* adjust if header is 60px */
  padding: 16px;
  box-sizing: border-box;
  overflow: hidden;

  margin-left: 250px; /* 👈 Add this line to push content next to sidebar */
}

.notifications-page {
  display: flex;
  margin-left: 250px; /* Match the width of your Sidebar */
  padding: 20px;
  gap: 20px;
}

.notification-content-container {
  display: flex;
  background: #fff;
  border-radius: 16px;
  overflow: hidden;
  height: 100%;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.05);
}

.notification-left-panel {
  width: 40%;
  background: #fff;
  padding: 24px;
  border-right: 1px solid #eee;
  overflow-y: auto;
}

.panel-title {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.notif-item {
  display: flex;
  align-items: center;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: background 0.2s;
}

.notif-item:hover {
  background: #f9f9f9;
}

.notif-item.active {
  background: #fff8dc;
  border-left: 4px solid #ffd84d;
}

.notif-check {
  width: 24px;
}

.circle {
  width: 16px;
  height: 16px;
  border: 2px solid #ccc;
  border-radius: 50%;
  display: inline-block;
}

.check {
  color: #fcd34d;
  font-weight: bold;
  font-size: 18px;
}

.notif-text {
  flex: 1;
  padding: 0 12px;
}

.notif-title {
  font-size: 0.9rem;
  font-weight: 600;
}

.notif-title.strike {
  text-decoration: line-through;
  color: #999;
}

.notif-msg {
  font-size: 0.8rem;
  color: #666;
}

.notif-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.notification-right-panel {
  width: 60%;
  padding: 32px;
  background: #fefefe;
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow-y: visible; /* ✅ Or remove this line entirely */
}

.task-title {
  font-size: 1.5rem;
  font-weight: 600;
}

.task-section {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
}

.tag {
  padding: 4px 10px;
  background: #eee;
  border-radius: 12px;
  font-size: 0.8rem;
}

.tag.dark {
  background: #000;
  color: #fff;
}

.tag.light {
  background: #e6e6e6;
}

.tag.medium {
  background: #fcd34d;
  color: #000;
}

.task-box h4 {
  margin-bottom: 4px;
}

.box-content {
  background: #f7f7f7;
  padding: 12px;
  border-radius: 10px;
  font-size: 0.85rem;
  display: flex;
  justify-content: space-between;
}

.task-footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
}

.btn {
  padding: 10px 16px;
  border-radius: 12px;
  font-size: 0.9rem;
  cursor: pointer;
  font-weight: 500;
  border: none;
}

.btn.outline {
  background: #f0f0f0;
}

.btn.danger {
  background: #ffe5e5;
  color: #a00000;
}
