@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

:root {
  --yellow: #fee56c;
  --yellow-light: #fff6db;
  --grey-line: #eaeaea;
  --font: 'Inter', sans-serif;
}

body {
  font-family: var(--font);
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  overflow: hidden;
}

/* ---------- Layout Wrapper ---------- */
.task-page-wrapper {
  margin-left: 250px;
  background: #f9f9f9;
  height: 100vh; /* Take full height */
  padding: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  overflow: hidden; /* prevent outer scroll */
}

/* ---------- Task Card Container ---------- */
.tasks‑wrapper {
  background: #fff;
  border-radius: 16px;
  padding: 28px 0 16px;
  width: 100%;
  box-shadow: 0 4px 14px rgba(0, 0, 0, .06);
  overflow-y: auto;
  flex: 1; /* fill remaining space */
  display: flex;
  flex-direction: column;
}

/* ---------- Header ---------- */
.tasks‑header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px 20px;
  border-bottom: 1px solid var(--grey-line);
}

.tasks‑header h2 {
  font-size: 22px;
  font-weight: 700;
  margin: 0;
}

.tasks‑filters button {
  padding: 6px 18px;
  border: none;
  background: transparent;
  border-radius: 16px;
  font-weight: 500;
  color: #6b7280;
  cursor: pointer;
  transition: background .18s, color .18s;
}

.tasks‑filters button.active,
.tasks‑filters button:hover {
  background: var(--yellow);
  color: #1a1a1a;
}

/* ---------- Section Heading ---------- */
.tasks‑section h3 {
  font-size: 17px;
  font-weight: 600;
  padding: 18px 32px 6px;
}

/* ---------- Grid Rows ---------- */
.row {
  display: grid;
  grid-template-columns: 24px 1fr 110px 110px 110px 130px 44px;
  align-items: center;
  padding: 10px 32px;
  font-size: 15px;
  border-bottom: 1px solid var(--grey-line);
}

.row:last-child {
  border-bottom: none;
}

.row.head {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: .03em;
  color: #868686;
  background: #fafafa;
}

/* ---------- Row Content ---------- */
.row .title {
  font-weight: 500;
}

.badge {
  padding: 2px 12px;
  border-radius: 12px;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
}

/* ---------- Badge Colors ---------- */
.stage.in‑progress {
  background: var(--yellow-light);
  color: #8d7424;
}

.stage.not‑started {
  background: #f0f0f0;
  color: #8d8d8d;
}

.priority.high {
  background: #e8b374;
  color: #643600;
}

.priority.medium {
  background: var(--yellow);
  color: #5b5500;
}

.priority.low {
  background: #e3eefc;
  color: #1a57b8;
}

.due {
  font-weight: 500;
}

.team {
  color: #626262;
}

/* ---------- Avatar ---------- */
.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

/* ---------- Checkbox ---------- */
input[type="checkbox"] {
  width: 18px;
  height: 18px;
  accent-color: var(--yellow);
  cursor: pointer;
}

/* ---------- Responsive ---------- */
@media (max-width: 720px) {
  .row,
  .row.head {
    grid-template-columns: 22px 1fr 90px 100px 100px 100px 40px;
    padding: 8px 16px;
  }

  .tasks‑header {
    flex-direction: column;
    gap: 14px;
    padding: 0 16px 18px;
  }

  .task-page-wrapper {
    margin-left: 0;
    padding: 16px;
  }
}
